import BbwzBaseView from "./BbwzBaseView";
import BbwzConstDefine, { BbwzRole } from "../data/BbwzConstDefine";
import BbwzScrollLabel from "../component/BbwzScrollLabel";
import BbwzBetManager from "../manager/BbwzBetManager";
import BbwzData from "../data/BbwzData";
import BbwzBasePool from "../tool/BbwzBasePool";
import BbwzPathHelper from "../tool/BbwzPathHelper";

// betArea_mix betArea_dragon betArea_tiger 区域命名与下注区域标识字符串对应
export default class BbwzBetAreaRootView extends BbwzBaseView{
    private areaMap: Map<string, BetAreaView> = new Map;
    private copyStarNode: cc.Node;
    private starPool: StarPool;
    private starNodeList: cc.Node[] = [];
    private showStarNode: cc.Node;
    private trendViewMap: Map<string, TrendView> = new Map;

    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        for(let i= 0; i < BbwzConstDefine.BET_AREA_NAME.length; i++){
            let key = BbwzConstDefine.BET_AREA_NAME[i];
            let node = this.getChild(`betArea_${key}`);
            let view = new BetAreaView(node);
            view.active = true;
            this.areaMap.set(key, view);

            let trendView = new TrendView(this.getChild(`trend_${key}`), key);
            trendView.active = true;
            this.trendViewMap.set(key, trendView);
        }

        this.copyStarNode = this.getChild("star");
        this.copyStarNode.active = false;
        this.starPool = new StarPool(this.getChild("starPool"), this.copyStarNode);
        this.showStarNode = this.getChild("showNode");
    }

    /**
     * 智多星下注
     * @param bAnim 是否播放动画
     * @param areaName 区域标识名
     * @param startWorldPos 起点世界坐标
     */
    public showStarBetEffect(bAnim: boolean, areaName: string, startWorldPos: cc.Vec2){
        if (!this.areaMap.has(areaName))
            return console.error("未定义area", areaName);

        let area = this.areaMap.get(areaName);
        if (area.isWiserBet)
            return;
        
        area.isWiserBet = true;
        let starNode = this.starPool.getItem();
        this.starNodeList.push(starNode);
        starNode.setParent(this.showStarNode);
        starNode.active = true;
        let desPoint = this.showStarNode.convertToNodeSpaceAR(area.getStarSetPos());
        if (!bAnim){
            starNode.setPosition(desPoint);
        }
        else{
            let startPos = this.showStarNode.convertToNodeSpaceAR(startWorldPos);
            starNode.setPosition(startPos);
            
            let time = desPoint.sub(startPos).mag() / 800;
            let dre = desPoint.x - startPos.x;
            // 最大高度
            let subPos = desPoint.sub(startPos);
            let maxHeight = Math.abs(desPoint.y - startPos.y);
            let randHeight = maxHeight * 1.2;  //Math.random() * maxHeight *dre;
            let ratioHeight = 0.5;
            if (subPos.x > 0) {
                if (subPos.y > 0 && dre > 0) {
                    randHeight = randHeight + subPos.y * ratioHeight;
                } else if (subPos.y < 0 && dre < 0) {
                    randHeight = randHeight + subPos.y * ratioHeight;
                }
            } else {
                if (subPos.y < 0 && dre > 0) {
                    randHeight = randHeight + subPos.y * ratioHeight;
                } else if (subPos.y > 0 && dre < 0) {
                    randHeight = randHeight + subPos.y * ratioHeight;
                }
            }
            let nRatio = 0.5;
            let q1Pos = cc.v2(subPos.x * nRatio + startPos.x, randHeight + startPos.y);
            let bezier = [startPos, q1Pos, desPoint];
            starNode.runAction(cc.bezierTo(time, bezier).easing(cc.easeSineOut()));
        }
    }

    public updateTotalBetLabel(data: any){
        this.areaMap.forEach((area, key)=>{
            if (data && data[key]){
                let totalBet = data[key].totalBetNum || 0;
                area.setTotalBet(Number(Global.Toolkit.formatPointStr(totalBet)));
            }
            else{
                Logger.error("找不到对应区域总下注数据", key);
            }
        })
    }

    /**
     * 
     * @param data 
     */
    public updateSelfBetLabel(data: any){
        this.areaMap.forEach((area, key)=>{
            if (data && data[key]){
                let selfBet = data[key].selfBetNum || 0;
                area.setSelfBet(Number(Global.Toolkit.formatPointStr(selfBet)));
            }
            else{
                Logger.error("找不到对应区域自己下注数据", key);
            }
        })
    }

    /**
     * 显示赢区域特效
     * @param data data.award.hit = {"fu_bull" : {hit: 0, multi: 1}, fu_zjh: {hit: 0, multi: 2}, lu_bull: {hit: 0, multi: 1}, ...}
     */
    public showAreaWinEffect(hit: any){
        if (!hit)
            return;
        for (let key in hit) {
            if (hit[key].hit == 1 && this.areaMap.has(key)){
                let area = this.areaMap.get(key);
                area.showHitEffect();
            }
        }
    }

    public showAreaTrend(areaName: string, list: any[], isAddNew: boolean){
        if (areaName){
            if (this.trendViewMap.has(areaName)){
                let trendView = this.trendViewMap.get(areaName);
                trendView.addItemData(list, isAddNew);
            }
        }
        else{
            this.trendViewMap.forEach(trendView=>{
                trendView.addItemData(list, isAddNew);
            })
        }
    }

    public clearByRound(){
        this.starPool.recycleAll(this.starNodeList);
        this.starNodeList = [];
        this.areaMap.forEach(area=>{
            area.reset();
        })
    }

    public clearByGame(){
        this.trendViewMap.forEach(trendView=>{
            trendView.clear();
        })
    }
}

class BetAreaView extends BbwzBaseView{
    private frameNode: cc.Node;
    private totalBetScrollLbl: BbwzScrollLabel;
    private selfBetScrollLbl: BbwzScrollLabel;
    private starPosNode: cc.Node;
    private _isWiserBet : boolean;

    constructor(node: cc.Node){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.frameNode = this.getChild("sprite_guang");
        this.frameNode.active = false;
        this.totalBetScrollLbl = Global.UIHelper.safeGetComponent(this.node, "label_total", BbwzScrollLabel);
        this.totalBetScrollLbl.goToNum(0, 0);
        this.selfBetScrollLbl = Global.UIHelper.safeGetComponent(this.node, "label_self", BbwzScrollLabel);
        this.selfBetScrollLbl.goToNum(0, 0);
        this.starPosNode = this.getChild("starPos");

        Global.UIHelper.addPolygonButton(this.getComponent("", cc.PolygonCollider));         // 不规则形状触摸监听
        this.node.on(cc.Node.EventType.TOUCH_END, this.onAreaClick, this);
    }

    // 下注区域点击
    private onAreaClick(eventTarget) {
        if (!BbwzData.instance.isBetEnable())
            return;
        
        let name: string = eventTarget.currentTarget.name;
        let betAreaName = name.replace("betArea_", "");
        let chipNum: number = BbwzData.instance.chipList[BbwzData.instance.currentSelectIndex];
        let bBet: boolean = BbwzData.instance.reqBet(chipNum, betAreaName);
        if (bBet)
            BbwzBetManager.addBetQueue(BbwzRole.Self, betAreaName, chipNum);
    }

    public setSelfBet(num: number){
        this.selfBetScrollLbl.goToNum(num);
    }

    public setTotalBet(num: number){
        this.totalBetScrollLbl.goToNum(num);
    }

    // 击中区域闪烁效果
    public showHitEffect(){
        this.frameNode.stopAllActions();
        this.frameNode.active = true;
        this.frameNode.opacity = 255;
        let actionArr = [];
        for (let i = 0; i < 6; i++) {
            let ac1 = cc.fadeOut(0.5);
            actionArr.push(ac1);
            let ac2 = cc.fadeIn(0.5);
            actionArr.push(ac2);
        }
        actionArr.push(cc.callFunc(()=>{
            this.frameNode.active = false;
        }, this));
        this.frameNode.runAction(cc.sequence(actionArr));
    }

    // 智多星飞在区域的世界坐标
    public getStarSetPos(){
        return this.starPosNode.parent.convertToWorldSpaceAR(this.starPosNode.position);
    }

    public get isWiserBet() : boolean {
        return this._isWiserBet;
    }

    public set isWiserBet(flag : boolean) {
        this._isWiserBet = flag;
    }

    public reset(){
        this.isWiserBet = false;
        this.frameNode.active = false;
        this.frameNode.stopAllActions();
        this.totalBetScrollLbl.goToNum(0, 0);
        this.selfBetScrollLbl.goToNum(0, 0);
    }
}

class StarPool extends BbwzBasePool<cc.Node>{
    constructor(root: cc.Node, private copyNode: cc.Node){
        super(root);
    }

    protected get preCount(){
        return 3;
    }

    protected createItem(){
        let node: cc.Node = cc.instantiate(this.copyNode);
        node.active = true;
        return node;
    }

    protected resetItem(node: cc.Node){
        node.setParent(this.root);
        node.active = false;
        node.stopAllActions();
    }
}

class TrendView extends BbwzBaseView{
    private layout: cc.Layout;
    private copySp: cc.Sprite;
    private itemSpList: cc.Sprite[] = [];
    private rawPos: cc.Vec2;
    private fullPos: cc.Vec2;
    private fullCount = 11;         // 多一个用以实现新增动画
    private curShowCount = 0;

    constructor(node: cc.Node, private key: string){
        super();
        this.setNode(node);
    }

    protected initView(){
        this.layout = this.getComponent("layout", cc.Layout);
        this.copySp = this.getComponent("layout/item", cc.Sprite);
        this.copySp.node.active = false;
        this.itemSpList.push(this.copySp);
        for (let i = 0; i < this.fullCount; i++){
            if (i == 0)
                continue;
            let node = cc.instantiate(this.copySp.node);
            node.setParent(this.layout.node);
            node.active = false;
            this.itemSpList.push(node.getComponent(cc.Sprite));
        }
        this.rawPos = this.layout.node.position;
        this.fullPos = this.rawPos.sub(cc.v2(this.copySp.node.width + this.layout.spacingX, 0));
    }

    public addItemData(arr: any[], isNewOne: boolean){
        if (!arr)
            return;
        if (arr.length <= 0)
            this.clearAll();
        if (!isNewOne){
            this.updateAll(arr);
        }
        else{
            let data = arr[arr.length - 1]["hit"];
            if(!data)
                return;
            this.showItem(this.curShowCount, data[this.key].hit);
            if (this.curShowCount == this.fullCount - 1){
                this.curShowCount = this.fullCount - 1;
                this.addExtraNewItemAnim();
            }
            else{
                this.curShowCount ++;
            }
        }
    }

    private updateAll(arr: any[]){
        this.layout.node.stopAllActions();
        this.layout.node.setPosition(this.rawPos);
        let total = arr.length;
        let maxShowCount = this.fullCount - 1;
        let showArr = arr.slice(total > maxShowCount ? total - maxShowCount : 0, total);
        this.itemSpList.forEach((itemSp, index)=>{
            if (showArr[index]){
                let hitData = showArr[index]["hit"];
                if (hitData && hitData[this.key]){
                    this.showItem(index, hitData[this.key].hit);
                }
            }
            else{
                itemSp.node.active = false;
            }
        })
        this.curShowCount = showArr.length;
    }

    private showItem(index: number, hit){
        if (!this.itemSpList[index])
            return;
        let str = hit == 0 ? "bbwz_cuo" : "bbwz_dui";
        this.itemSpList[index].node.active = true;
        Global.ResourceManager.loadBundleAutoAtlas(BbwzConstDefine.GAME_ID,this.itemSpList[index], BbwzPathHelper.gameTexturePath + "atlas/dynamic/atlas_dynamic", str, null, true);
    }

    private addExtraNewItemAnim(){
        this.layout.node.setPosition(this.rawPos);
        let moveAct = cc.moveTo(0.5, this.fullPos).easing(cc.easeBackOut());
        let callFunc = cc.callFunc(()=>{
            let item = this.itemSpList.shift();
            item.node.removeFromParent(false);
            item.node.setParent(this.layout.node);
            this.itemSpList.push(item);
            item.node.active = false;
            this.layout.node.setPosition(this.rawPos);      // 滚动动画后, 将原先第一个节点移至最后
        }, this);

        this.layout.node.runAction(cc.sequence([moveAct, callFunc]));
    }

    private clearAll(){
        this.itemSpList.forEach(sp=>{
            sp.node.active = false;
        })
        this.curShowCount = 0;
        this.layout.node.stopAllActions();
        this.layout.node.setPosition(this.rawPos);
    }

    public clear(){
        this.clearAll();
    }
}